

$메인칼라 : #ff0000;

.red {
    color : $메인칼라;
}

@mixin 함수 (){
    background :#eeeeee;
    padding : 20px;
    border-radius : 5px;//둥글게
    max-width : 500px;
    width : 100%;
    margin : auto; //가운데 정렬
}

.my-alert {
    @include 함수()
}

.my-alert2 {
    @extend .my-alert;
    background : #ffe591;
}

.my-alert p {
    margin-bottom : 0;
}

.wow-enter {
    opacity : 0; // 투명도 0~1
}
.wow-enter-active {
    opacity : 1;
    transition : all 500ms; // opacity가 0에서 1까지 서서히 0.5초에 걸쳐서 변함

}


